"use strcit"
const header = document.querySelector(".header-page");

window.addEventListener("scroll", () => {
  let scrollDistance = window.scrollY;
  if (scrollDistance >= 140) {
    header.classList.add("header-page--fixed");

  } else header.classList.remove("header-page--fixed");
})


const burgerIcon = document.querySelector(".burger-icon");
const bugerIconLines= document.querySelectorAll(".burger-icon__line");
const burgerMenuLinks = document.querySelectorAll(".menu__list--item");
const menu = document.querySelector(".menu__list");

function changeBurgerIcon () {
    bugerIconLines.forEach((element) => {
        element.classList.toggle("burger-icon__line--active");
    })
}

burgerIcon.addEventListener("click", () =>{
        changeBurgerIcon ();
        menu.classList.toggle("menu__list--active");
        body.classList.toggle("body-page__no-scroll")
    
  })


document.addEventListener("click", (event) => {
    let target = event.target;
    let headerMenu = target == menu || menu.contains(target);
    let burger = target == burgerIcon || target.closest(".burger-icon");
    let headerMenuActive = target.classList.contains('menu__list--link');

    if (headerMenu && !burger && !headerMenuActive && window.screen.availWidth<993) {
        menu.classList.toggle("menu__list--active");
        changeBurgerIcon ();
        body.classList.toggle("body-page__no-scroll");
    } else if (headerMenuActive && window.screen.availWidth<993){
        menu.classList.remove("menu__list--active");
        changeBurgerIcon ();
        body.classList.remove("body-page__no-scroll");
    }
})


      

window.addEventListener("resize", (event) => {
    if (event.target.screen.availWidth > 993) {
        menu.classList.remove('menu__list--active');
        bugerIconLines.forEach((element) => {
            element.classList.remove("burger-icon__line--active");
        })
    }
    if (!menu.classList.contains("menu__list--active")) {
    body.classList.remove("body-page__no-scroll");
}
})

const body = document.querySelector(".body-page");


// ---------------------------------------------------------Menu---------------------------------------------------------

const menuList = document.querySelector(".food-list");
const menuItems = document.querySelectorAll(".food-list__item");

menuList.addEventListener("click", ((event)=> {
    if (event.target.classList.contains("food-list__item")) {
        menuItems.forEach((item) => {
            item.classList.remove("food-list__item--active");
            event.target.classList.add("food-list__item--active");
        })
    }
})
)


//---------------------------------------------------- Testimonals----------------------------------------------------

const slider = document.querySelector(".slider");
const sliderItems = document.querySelectorAll(".slider__item");
const userDescription = document.querySelectorAll(".testimonial-list__item");

slider.addEventListener ("click", (event) => {
    if (event.target.classList.contains("slider__item")) {
        sliderItems.forEach((item) => {
            item.classList.remove("slider__item--active");
            event.target.classList.add("slider__item--active");
        }) 
        userDescription.forEach((element) => {
            if (event.target.getAttribute("data-counter") !== element.getAttribute("data-counter")) {
                element.classList.remove("testimonial-list__item--active");
            }else {
                        element.classList.add("testimonial-list__item--active");
                      }
        })
    }

})


